var watchTabSwitch = function () {
    document.addEventListener('visibilitychange', function () {
        var isPageActive = !document.hidden;
        if (!isPageActive) {
            var normalizedTitle = document.title.replace(/^(\(1\) )/gi, '');
            // @ts-ignore
            document.title = "".concat(window._chat_new_messages_ ? "(".concat(window._chat_new_messages_, ")") : '', " ").concat(normalizedTitle);
            return;
        }
        document.title = document.title.replace(/^\(\d\)/i, '');
    });
};
export default watchTabSwitch;
